import API from './API';

const ProfileService = {
    setUserBasicInfo: (userData) => {
        const requestData = {
            name: userData.name,
            age: userData.age,
            gender: userData.gender === 'None' ? null : userData.gender,
        };

        return API.put('/profiles/user/set/basic-info/', requestData)
            .then((response) => {
                if (response.status === 200) {
                    console.log('    .');
                }
                return response.data;
            })
            .catch((error) => {
                if (error.response && error.response.data) {
                    console.error('API Error:', error.response.data);
                    throw new Error('     .');
                } else if (error.response) {
                    throw new Error('  .');
                } else {
                    throw new Error(error.message);
                }
            });
    },

    setUserProfileInfo: (userData) => {
        const formData = new FormData();
        formData.append('image', userData.profileImage);

        return API.put('/profiles/user/set/profile-image/', formData)
            .then((response) => {
                if (response.status === 200) {
                    console.log('   .');
                }
                return response.data;
            })
            .catch((error) => {
                if (error.response && error.response.data) {
                    console.log(error.response);
                    throw new Error('     .');
                } else if (error.response) {
                    throw new Error('  .');
                } else {
                    throw new Error(error.message);
                }
            });
    },

    fetchWorkStyles: () => {
        return API.get(`/profiles/workstyles/`)
            .then((response) => response.data)
            .catch((error) => {
                console.error('     .', error);
                throw error;
            });
    },

    setUserWorkStyles: (workStyles) => {
        const requestData = {
            work_styles: workStyles,
        };

        return API.put(`/profiles/user/set/work-style/`, requestData)
            .then((response) => {
                if (response.status === 200) {
                    console.log('    .');
                }
                return response.data;
            })
            .catch((error) => {
                console.error('     .', error);
                throw error;
            });
    },

    fetchInterests: () => {
        return API.get(`/profiles/interests/`)
            .then((response) => response.data)
            .catch((error) => {
                console.error('     .', error);
                throw error;
            });
    },

    setUserInterest: (interests) => {
        const requestData = {
            interests: interests,
        };

        return API.put(`/profiles/user/set/interest/`, requestData)
            .then((response) => {
                if (response.status === 200) {
                    console.log('    .');
                }
                return response.data;
            })
            .catch((error) => {
                console.error('     .', error);
                throw error;
            });
    },

    fetchUserProfile: () => {
        return API.get(`/profiles/user/current`)
            .then((response) => response.data)
            .catch((error) => {
                console.error('     .', error);
                throw error;
            });
    },

    getUserProfileLink: (username) => {
        return `${window.location.origin}/friend-profile/${username}`;
    },

    fetchFriendProfile: (username) => {
        return API.get(`/profiles/user/view/${username}`)
            .then((response) => {
                if (response.status === 200) {
                    return response.data;
                }
                throw new Error('     .');
            })
            .catch((error) => {
                console.error('     .', error);
                throw error;
            });
    },

    fetchLongQuestions: (username) => {
        return API.get(`/profiles/user/view/long-questions/${username}`)
            .then((response) => {
                if (response.status === 200) {
                    console.log('    .');
                }
                return response.data;
            })
            .catch((error) => {
                console.error('      .', error);
                throw error;
            });
    },

    fetchFriends: (username) => {
        return API.get(`/profiles/user/view/friends/${username}`)
            .then((response) => {
                if (response.status === 200) {
                    console.log('    .');
                }
                return response.data;
            })
            .catch((error) => {
                console.error('      .', error);
                throw error;
            });
    },
    fetchSearchResults: async (query) => {
        try {
            const response = await API.get('/profiles/user/search', {
                params: { q: query },
            });
            return response.data;
        } catch (error) {
            console.error('Error fetching search results:', error);
            throw error;
        }
    },
    followUser: (friend_name) => {
        return API.post(`/profiles/user/follow/`, { friend_name })
            .then((response) => response.data)
            .catch((error) => {
                console.error('   .', error);
                throw error;
            });
    },

    unfollowUser: (friend_name) => {
        return API.post(`/profiles/user/unfollow/`, { friend_name })
            .then((response) => response.data)
            .catch((error) => {
                console.error('    .', error);
                throw error;
            });
    },

    // s3  Service
    getSignedImageUrl: (imagePath) => {
        return API.get(`/profiles/user/get-signed-url/${encodeURIComponent(imagePath)}/`)
            .then((response) => {
                if (response.status === 200) {
                    return response.data.signed_url;
                }
                throw new Error('Failed to fetch signed URL.');
            })
            .catch((error) => {
                throw error;
            });
    },
};

export default ProfileService;
