import API from './API';

const AdminService = {
    registerUser: (userData) => {
        const requestData = {
            username: userData.username,
            password1: userData.password1,
            password2: userData.password2,
        };

        return API.post('/profiles/auth/registration/', requestData)
            .then((response) => {
                if (response.status === 204) {
                    console.log('  .');
                }
            })
            .catch((error) => {
                if (error.response && error.response.data) {
                    if (error.response.data.username) {
                        throw new Error('  .');
                    }
                    if (error.response.data.password1) {
                        throw new Error('    8  .');
                    }
                    if (error.response.data.non_field_errors) {
                        throw new Error('   .');
                    }
                } else if (error.response) {
                    throw new Error('  .');
                } else {
                    throw new Error(error.message);
                }
            });
    },

    checkUserName: (userName) => {
        return API.post('/profiles/auth/check-username/', userName)
            .then((response) => {
                return response;
            })
            .catch((error) => {
                throw new Error(' 3  30  .');
            });
    },

    login: (userData) => {
        const requestData = {
            username: userData.username,
            password: userData.password,
        };

        return API.post('/profiles/auth/login/', requestData)
            .then((response) => {
                if (response.status === 200) {
                    const token = response.data.key;
                    localStorage.setItem('authToken', token);
                }
            })
            .catch((error) => {
                let errorMessage = '    .';
                if (error.response && error.response.data) {
                    if (error.response.data.message) {
                        errorMessage = error.response.data.message;
                    } else {
                        errorMessage = error.response.data.errors.non_field_errors[0];
                    }
                } else if (error.response) {
                    errorMessage = '  .';
                }
                throw new Error(errorMessage);
            });
    },

    logout: () => {
        return API.post('/profiles/auth/logout/')
            .then((response) => {
                if (response.status === 200) {
                    //  200
                    console.log('  .');
                    localStorage.removeItem('authToken'); // Remove the token from local storage
                }
            })
            .catch((error) => {
                throw new Error(error.message);
            });
    },

    userDelete: () => {
        return API.delete('/profiles/auth/delete/')
            .then((response) => {
                if (response.status === 200) {
                    console.log('   .');
                }

                return response.data;
            })
            .catch((error) => {
                console.error('    .', error);
                console.error(' :', error.response);
                console.error(' :', error.message);
            });
    },
};

export default AdminService;
